import { Injectable } from '@angular/core';
import { Auth } from '@angular/fire/auth';
import { doc, docData, Firestore, setDoc } from '@angular/fire/firestore';
import {
  getDownloadURL,
  deleteObject,
  ref,
  Storage,
  uploadString,
  uploadBytes
} from '@angular/fire/storage';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  constructor(
    private auth: Auth,
    private firestore: Firestore,
    private storage: Storage
  ) { }

  getUserProfile() {
    const user = this.auth.currentUser;
    const userDocRef = doc(this.firestore, `users/${user.uid}`);
    return docData(userDocRef, { idField: 'id' });
  }

  async uploadCourse(cameraFile: any) {
    const user = this.auth.currentUser;
    const path = `uploads/${user.uid}/profile.png`;
    const storageRef = ref(this.storage, path);

    try {
      await uploadString(storageRef, cameraFile.base64String, 'base64');

      const imageUrl = await getDownloadURL(storageRef);

      const userDocRef = doc(this.firestore, `users/${user.uid}`);
      await setDoc(userDocRef, {
        imageUrl,
      });
      return true;
    } catch (e) {
      return null;
    }
  }

  async uploadReview(data: any) {
    const user = this.auth.currentUser;
    const path = `uploads/reviews/${data.name}`;
    const storageRef = ref(this.storage, path);
    try {
      await uploadBytes(storageRef, data.file);
      const fileURL = await getDownloadURL(storageRef);
      return { file_url: fileURL };
    } catch (e) {
      return { error: e };
    }
  }

  async pushFileToStorage(data: any, filePath: string = undefined) {

    const user = this.auth.currentUser;
    const path = filePath ? filePath : `uploads/test/${data.name}`;
    const storageRef = ref(this.storage, path);

    try {
      await uploadBytes(storageRef, data.file);

      const fileURL = await getDownloadURL(storageRef);

      // const testDocRef = doc(this.firestore, `tests/${data.test_id}`);
      // await setDoc(testDocRef, { file_url: fileURL, updated_at: serverTimestamp() });
      return { file_url: fileURL };
    } catch (e) {
      return { error: e };
    }
  }

  async deleteFile(path: string) {
    const storageRef = ref(this.storage, path);
    deleteObject(storageRef).then((resp) => resp).catch((error) => error);
  }
}
